import React, { Component } from 'react';
import "../css/App.css";
import jQuery from "jquery";
import { Subject } from './ChooseSubject';
import { Client } from './ClientInformation';
import { KeyValue } from '../types';


export interface QuotationLine {
  count: number;
  product: string;
  price: number;
}

export interface PaymentProps {
  subject: Subject;
  client: Client;
  quotation: {
    productId: string | null;
    token: string | null;
    myPrice: string;
  }
  onFieldChange(event: KeyValue): void;
  onQuotationReloaded(myPrice: string, email: string): void;
  stepButtonStatus(prevStatus: boolean, nextStatus: boolean): void
}

export interface PaymentState {
  token: string;
  myPrice: string;
  quotationLines: QuotationLine[] | null;
  tax: number;
  total: number;
  paymentStatus: string;
  paying: boolean;
  validMyPrice: boolean;
  percentage: number;
  gettingPaymentUrl: boolean;
  // attentionBalloonVisible: boolean;
  email: string;
  clientSidePrice: boolean;
}

export default class Payment extends Component<PaymentProps, PaymentState> {

  watchTimeout: any;

  constructor(props: PaymentProps) {
    super(props)
    this.getQuotation = this.getQuotation.bind(this)
    this.state = {
      token: "",
      myPrice: "",
      quotationLines: null,
      tax: 0,
      total: 0,
      paymentStatus: "",
      paying: false,
      validMyPrice: true,
      percentage: 50,
      gettingPaymentUrl: false,
      // attentionBalloonVisible: false,
      email: "",
      clientSidePrice: false
    }

    this.payQuotation = this.payQuotation.bind(this);
    this.getQuotation = this.getQuotation.bind(this);
    this.reloadQuotation = this.reloadQuotation.bind(this);
    this.checkPayment = this.checkPayment.bind(this);
    this.startWatching = this.startWatching.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.updatePercentage = this.updatePercentage.bind(this);
  }

  componentDidMount() {

    this.props.stepButtonStatus(true, false)
    if (this.props.quotation.token === null) {
      this.getQuotation(this.props)
    }
    else {
      this.reloadQuotation(this.props.quotation.token)
    }
  }

  componentWillReceiveProps(nextProps: PaymentProps) {
    if (this.props != nextProps) {
      console.log("component will receiveprops");
      this.setState({
        myPrice: nextProps.quotation.myPrice
      });
    }
  }

  render() {
    console.log("Payment component State :", this.state)
    console.log("Payment component Props :", this.props)
    let key = 0;
    return (<div className="paymentComponent">
      {(!this.state.quotationLines || this.state.paying || this.state.gettingPaymentUrl) &&
        <div className="overlay">
          {(!this.state.quotationLines || this.state.paying || this.state.gettingPaymentUrl) && <div className="lds-dual-ring"></div>}
        </div>}
      <div className="formContainer">

        {this.state && this.state.quotationLines && <table className="QuotationTable">
          <thead>
            <tr key={key++} className="QuotationLineHeader">
              <td className="QuotationAantal">Aantal</td>
              <td className="QuotationProduct">Product</td>
              <td className="QuotationPrijs">Prijs</td>
              <td></td>
            </tr>
          </thead>
          <tbody>
            {this.state && this.state.quotationLines && this.state.quotationLines.map((quotationLine) => {
              return (<tr key={key++} className="QuotationLine">
                <td className="QuotationAantal">{quotationLine.count}</td>
                <td className="QuotationProduct">{quotationLine.product}</td>
                <td className="QuotationPrijs">{Number(quotationLine.price).toLocaleString("nl-NL", { minimumFractionDigits: 2 })}</td>
                <td></td>
              </tr>)
            })}

            <tr className="QuotationLine">
              <td>
              </td>
              <td className="QuotationExtra">BTW</td>
              <td className="QuotationPrijs ul">{(this.state.tax).toLocaleString("nl-NL", { minimumFractionDigits: 2 })}</td>
              <td>+</td>
            </tr>
            <tr className="QuotationLine">
              <td>
              </td>
              <td className="QuotationExtra">Totaal</td>
              <td className="QuotationPrijs">{(this.state.total).toLocaleString("nl-NL", { minimumFractionDigits: 2 })}</td>
              <td></td>
            </tr>
            {this.state.clientSidePrice &&
              <tr className="QuotationLine">
                <td>
                </td>
                <td className="QuotationExtra">"Pay-what-you-want"-korting</td>
                <td className="QuotationMijnPrijs ul">{(this.state.percentage).toLocaleString("nl-NL", { maximumFractionDigits: 2 })}% </td>
                <td>-</td>
              </tr>}
            {this.state.paymentStatus !== "paid" && !this.state.paying && this.state.clientSidePrice &&
              <tr className="QuotationLine">
                <td>
                </td>
                <td className="QuotationExtra">Dit wil ik betalen</td>
                <td className="QuotationMijnPrijs">
                  <input id="myPrice"
                    className="myPrice"
                    value={this.state.myPrice}
                    onChange={this.handleChange}>
                  </input>
                </td>
                <td></td>
              </tr>
            }
            {(this.state.paymentStatus === "paid" || this.state.paying) && this.state.clientSidePrice &&
              <tr className="QuotationLine">
                <td>
                </td>
                <td className="QuotationExtra">Betaald</td>
                <td className="QuotationMijnPrijs ul">{(Number(this.state.myPrice)).toLocaleString("nl-NL", { maximumFractionDigits: 2 })} </td>
                <td className="QuotationMijnPrijs ul">{this.state.myPrice}</td>
                <td></td>
              </tr>
            }
          </tbody>
        </table>}
        {/* {this.state.attentionBalloonVisible && (!this.state.paying) && (!this.state.gettingPaymentUrl) && <div className="attentionBalloon">Aanbieding !</div>} */}

        {this.state.quotationLines && !this.state.paying && (this.state.paymentStatus != "paid") &&
          <button className="payButton" onClick={this.payQuotation} disabled={!this.state.validMyPrice}>Afrekenen</button>}
        {this.state.paymentStatus === "paid" && <div className="paid">U heeft betaald...</div>}
      </div>
    </div>
    );
  }


  handleChange(event: (React.ChangeEvent<HTMLInputElement> | React.ChangeEvent<HTMLSelectElement>)): void {
    let numberRegEx = /^([0-9]*)(,[0-9]{2})?$/g
    this.setState(
      {
        validMyPrice: event.target.value.match(numberRegEx) !== null
      }, this.updatePercentage
    )
    this.props.onFieldChange({ [event.target.id]: event.target.value })

    //this.props.stepButtonStatus(false, event.target.value.match(numberRegEx) !== null)
  }

  updatePercentage() {
    if (this.state.validMyPrice) {
      this.setState({
        percentage: 100 - (Number(this.state.myPrice.replace(",", ".")) / this.state.total * 100)
      })
    }

  }


  getQuotation(data: PaymentProps) {
    let componentHandle = this
    jQuery.ajax({
      type: "POST",
      url: "https://us-central1-salustest-7df6a.cloudfunctions.net/getQuotation",
      contentType: "application/json; charset=utf-8",
      dataType: "json",
      data: JSON.stringify(data),
      success: (result) => {
        console.log(result)
        componentHandle.setState({
          token: result.token,
          quotationLines: result.quotationLines,
          myPrice: (result.total / 2).toLocaleString("nl-NL", { minimumFractionDigits: 2 }),
          tax: result.tax,
          total: result.total,
          clientSidePrice: result.clientSidePrice
        })
        // if (result.clientSidePrice) {
        //   setTimeout(() => {
        //     componentHandle.setState({
        //       attentionBalloonVisible: true
        //     })
        //   }, 2000)
        // }
      },
      error: (error) => {
        console.log(error)
      }
    })
  }


  reloadQuotation(token: string) {
    this.props.stepButtonStatus(false, false)
    let componentHandle = this
    let data = {
      token: token
    }
    jQuery.ajax({
      type: "POST",
      url: "https://us-central1-salustest-7df6a.cloudfunctions.net/reloadQuotation",
      contentType: "application/json; charset=utf-8",
      dataType: "json",
      data: JSON.stringify(data),
      success: (result) => {
        console.log("reloadQuotation:", result)
        let discount = (result.total - result.myPrice) / result.total * 100
        componentHandle.setState({
          token: result.token,
          quotationLines: JSON.parse(result.qlines),
          paymentStatus: result.status,
          myPrice: (result.myPrice).toLocaleString("nl-NL", { minimumFractionDigits: 2 }),
          email: result.email,
          tax: result.tax,
          percentage: discount,
          total: result.total,
          paying: (result.status === "created")
        }, () => {
          this.props.onQuotationReloaded(this.state.myPrice, this.state.email)
          this.props.stepButtonStatus(result.status !== "paid", result.status === "paid")
        })
      },
      error: (error) => {
        console.log(error)
      }
    })
  }


  payQuotation() {
    let componentHandle = this
    this.setState({
      gettingPaymentUrl: true
    })

    console.log("PAYING QUOTATION NOW>>>>")
    this.props.stepButtonStatus(false, false)

    let data = {
      token: this.state.token,
      myPrice: this.state.myPrice,
      srcUrl: window.location.href
    }

    console.log("PAYMENTDATA:", data)
    jQuery.ajax({
      type: "POST",
      url: "https://us-central1-salustest-7df6a.cloudfunctions.net/payQuotation",
      contentType: "application/json; charset=utf-8",
      dataType: "json",
      data: JSON.stringify(data),
      success: (result) => {
        console.log(result)
        componentHandle.setState({
          gettingPaymentUrl: false
        })
        window.location.href = result.url
      },
      error: (error) => {
        console.log(error)
      }
    })
  }

  checkPayment() {
    let componentHandle = this
    let data = {
      token: this.state.token
    }
    jQuery.ajax({
      type: "POST",
      url: "https://us-central1-salustest-7df6a.cloudfunctions.net/checkPayment",
      contentType: "application/json; charset=utf-8",
      dataType: "json",
      data: JSON.stringify(data),
      success: (result) => {
        console.log("CHECKPAYMENTSTATUSRESULT:", result)
        if (!result[0] || result[0].status === "created") {
          this.watchTimeout = setTimeout(this.checkPayment, 50);
        }
        componentHandle.setState({
          paymentStatus: result[0].status,
          paying: (result[0].status === "created")
        })
      },
      error: (error) => {
        console.log(error)
      }
    })
  }

  startWatching() {
    this.setState({
      paying: true,
    })
    this.watchTimeout = setTimeout(this.checkPayment, 50);
  }
}