import React, { Component } from 'react';
import jQuery from "jquery";
import "../css/App.css";
import '../css/linearicons.css'
import { KeyValue } from '../types';


export interface Client {
  name: string;
  company:string;
  address:string;
  city:string;
  country:string;
  zipcode:string;
  email: string;
  telephone: string;
}

export interface ClientInformationProps {
  client: Client;
  onFieldChange(newField: KeyValue): void;
  stepButtonStatus(prevStatus: boolean, nextStatus: boolean): void
}

export interface ClientInformationState {
  nameValid: boolean;
  emailValid: boolean;
  phoneNumberValid: boolean;
  cityValid:boolean;
  addressValid:boolean;
  zipCodeValid:boolean;
}

export default class ClientInformation extends React.Component<ClientInformationProps, ClientInformationState> {

  constructor(props: ClientInformationProps) {
    super(props);
    this.state = {
      nameValid: this.validName(props.client.name),
      emailValid: this.validEmailAddress(props.client.email),
      phoneNumberValid: this.validPhoneNumber(props.client.telephone),
      cityValid:this.validCity(props.client.city),
      addressValid:this.validAddress(props.client.address),
      zipCodeValid:this.validZipCode(props.client.zipcode)

    }
    this.handleChange = this.handleChange.bind(this);
    this.validEmailAddress = this.validEmailAddress.bind(this);
    this.validPhoneNumber = this.validPhoneNumber.bind(this);
    this.validName = this.validName.bind(this);
  }

  componentDidMount() {
    this.props.stepButtonStatus(true, this.state.emailValid && this.state.nameValid && this.state.phoneNumberValid)
    //jQuery('#telephone').on('blur input', this.validate);

  }


  componentDidUpdate(prevProps: ClientInformationProps, prevState: ClientInformationState) {
    if ((prevState.emailValid !== this.state.emailValid) ||
      (prevState.nameValid !== this.state.nameValid) ||
      (prevState.phoneNumberValid !== this.state.phoneNumberValid)) {
      this.props.stepButtonStatus(true, this.state.emailValid && this.state.nameValid && this.state.phoneNumberValid)
    }
  }



  render() {
    console.log("CI PROPS:", this.props)
    console.log("CI STATE:", this.state)
    return (
      <div className="formContainer">
        <div className="checkedField">
          <input id="name" type="text" placeholder="contactpersoon"
            value={this.props.client.name} onChange={this.handleChange} />
          {this.state.nameValid ? <span className="lnr lnr-checkmark-circle" /> : <span className="lnr lnr-cross-circle" />}
        </div>
        <div className="checkedField">
          <input id="company" type="text" placeholder="bedrijfsnaam"
            value={this.props.client.company} onChange={this.handleChange} />
          {this.state.nameValid ? <span className="lnr lnr-checkmark-circle" /> : <span className="lnr lnr-cross-circle" />}
        </div>
        <div className="checkedField">
          <input id="address" type="text" placeholder="adres"
            value={this.props.client.address} onChange={this.handleChange} />
          {this.state.addressValid ? <span className="lnr lnr-checkmark-circle" /> : <span className="lnr lnr-cross-circle" />}
        </div>
        <div className="checkedField">
          <input id="city" type="text" placeholder="vestigingsplaats"
            value={this.props.client.city} onChange={this.handleChange} />
          {this.state.cityValid ? <span className="lnr lnr-checkmark-circle" /> : <span className="lnr lnr-cross-circle" />}
        </div>
        <div className="checkedField">
          <input id="zipcode" type="text" placeholder="postcode"
            value={this.props.client.zipcode} onChange={this.handleChange} />
          {this.state.zipCodeValid ? <span className="lnr lnr-checkmark-circle" /> : <span className="lnr lnr-cross-circle" />}
        </div>
        <div className="checkedField">
          <input id="country" type="text" placeholder="land"
            value={this.props.client.country} onChange={this.handleChange} />
          {this.state.nameValid ? <span className="lnr lnr-checkmark-circle" /> : <span className="lnr lnr-cross-circle" />}
        </div>
        <div className="checkedField">
          <input id="email" placeholder="emailadres"
            value={this.props.client.email} onChange={this.handleChange} />
          {this.state.emailValid ? <span className="lnr lnr-checkmark-circle" /> : <span className="lnr lnr-cross-circle" />}
        </div>
        <div className="checkedField">
          <input id="telephone" placeholder="telefoonnummer"
            value={this.props.client.telephone} onChange={this.handleChange} />
          {this.state.phoneNumberValid ? <span className="lnr lnr-checkmark-circle" /> : <span className="lnr lnr-cross-circle" />}
        </div>
      </div>
    );
  }

  handleChange(event: (React.ChangeEvent<HTMLInputElement> | React.ChangeEvent<HTMLSelectElement>)): void {
    if (event.target.id === "email") {
      this.setState({
        emailValid: this.validEmailAddress(event.target.value)
      });
    }
    if (event.target.id === "telephone") {
      this.setState({
        phoneNumberValid: this.validPhoneNumber(event.target.value)
      });
    }
    if (event.target.id === "name") {
      this.setState({
        nameValid: this.validName(event.target.value)
      });
    }

    if (event.target.id === "address") {
      this.setState({
        addressValid: this.validAddress(event.target.value)
      });
    }

    if (event.target.id === "city") {
      this.setState({
        cityValid: this.validCity(event.target.value)
      });
    }

    if (event.target.id === "zipcode") {
      this.setState({
        zipCodeValid: this.validZipCode(event.target.value)
      });
    }
    this.props.onFieldChange({ [event.target.id]: event.target.value })

  }


  validEmailAddress(mailAddress: string) {
    let emailRegEx = /^(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])$/
    return (mailAddress.match(emailRegEx) !== null);
  }


  validPhoneNumber(phoneNumber: string) {
    let phoneNumberRegEx = /^((?:\+|00)[17](?: |\-)?|(?:\+|00)[1-9]\d{0,2}(?: |\-)?|(?:\+|00)1\-\d{3}(?: |\-)?)?(0\d|\([0-9]{3}\)|[1-9]{0,3})(?:((?: |\-)[0-9]{2}){4}|((?:[0-9]{2}){4})|((?: |\-)[0-9]{3}(?: |\-)[0-9]{4})|([0-9]{7}))/g
    return (phoneNumber.match(phoneNumberRegEx) !== null);
  }

  validAddress(name: string) {
    let nameRegEx = /.{2,}?/g
    return (name.match(nameRegEx) !== null);
  }

  validCity(name: string) {
    let nameRegEx = /.{2,}?/g
    return (name.match(nameRegEx) !== null);
  }

  validZipCode(name: string) {
    let nameRegEx = /.{2,}?/g
    return (name.match(nameRegEx) !== null);
  }

  validName(name: string) {
    let nameRegEx = /.{2,}? .{2,}/g
    return (name.match(nameRegEx) !== null);
  }
}






