import React, { Component } from 'react';
import jQuery from "jquery";
import { Client } from './ClientInformation';
import { isNull } from 'util';

export interface DownloadProps {
  client: Client;
  token: string | null;
}

export interface DownloadState {
  reportCreationStatus: string;
  startedTime: Date;
  timedOut:boolean;
}


export default class Download extends Component<DownloadProps, DownloadState> {

  watchTimeout: any;

  constructor(props: DownloadProps) {
    super(props)
    this.getReportCreationStatus = this.getReportCreationStatus.bind(this)
    this.state = {
      reportCreationStatus: "paid",
      startedTime: new Date(),
      timedOut: false
    }
  }

  componentDidMount() {
    this.getReportCreationStatus()

  }

  render() {
    let downloadLink = "https://us-central1-salustest-7df6a.cloudfunctions.net/downloadSanctionFile?token=" + this.props.token
    return (
      <div className="DownloadForm">
        {this.state.reportCreationStatus === "paid" && <div className="downloadInfo">
          Uw screening wordt op dit moment uitgevoerd. <b>Dit kan tot 1 minuut duren.</b> <br /> Zodra het rapport gereed is, kunt u het hieronder downloaden.
          Daarnaast zal er een copy van het rapport gemaild worden naar {this.props.client.email}
        </div>}
        {this.state.reportCreationStatus === "ready" && <div className="downloadInfo">
          Uw rapport is gereed om te downloaden.
          Een copy van het rapport is ook gemaild naar {this.props.client.email}
        </div>}
        {this.state.reportCreationStatus=== "downloadtimeout" && <div className="downloadInfo">Er is iets mis gegaan bij het klaarzetten van uw rapport.
          U kunt uw rapport alsnog downloaden vanuit de mail die verzonden wordt naar: {this.props.client.email}</div>}
        <div className="downloadContainer">
          {this.state.reportCreationStatus === "ready" && <a href={downloadLink} download>Download</a>}
          {this.state.reportCreationStatus === "paid" && <div className="downLoadSpinner"><div className="lds-dual-ring"></div></div>}
        </div>
      </div>
    );
  }

  getReportCreationStatus() {
    let componentHandle = this
    let data = {
      token: this.props.token
    }
    jQuery.ajax({
      type: "POST",
      url: "https://us-central1-salustest-7df6a.cloudfunctions.net/checkPayment",
      contentType: "application/json; charset=utf-8",
      dataType: "json",
      data: JSON.stringify(data),
      success: (result) => {
        console.log("CHECKPAYMENTSTATUSRESULT:", result)
        if (!result[0] || result[0].status !== "ready") {
          if (Math.floor((new Date().getTime() - this.state.startedTime.getTime()) / 60000) < 1) {
            this.watchTimeout = setTimeout(this.getReportCreationStatus, 50);
          }
          else {
            this.setState({timedOut:true})
          }
        }
        componentHandle.setState({
          reportCreationStatus: result[0].status,
        })
      },
      error: (error) => {
        console.log(error)
        if (Math.floor((new Date().getTime() - this.state.startedTime.getTime()) / 60000) < 1) {
          this.watchTimeout = setTimeout(this.getReportCreationStatus, 1000);
        }
        else {
          this.setState({reportCreationStatus:"downloadtimeout"})
        }
    }
    })
  }
}